import React from 'react';

import "./Testimonials.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'






const Testimonials = () =>{
    return(
        <div>
           
           <h2>TESTIMONIALS</h2>
         
           <div className='testimonial'>
           <div id= "one" className='testimonials'>
               <p>
               I have been working with Lynne and Dynamic Medical Services for about 10 years. 
               Lynne was hired to perform billing for my Partnership and she immediately found 
               a substantial amount of OHIP and non-OHIP revenue that had not been billed plus
                many unpaid transactions. We were able to recover a substantial amount of money 
                from the preceding 6 months. I shudder to think of the income that was lost prior
                 to that. With Lynne’s attention to detail our billing became thorough, accurate 
                 and complete. The increase in income was appreciated by all partners. Lynne was 
                 always educating us about the fee schedule and making sure we were coding all
                  billable activities. As a long-term client of Dynamic Medical Services, I am
                   confident that my billing is well done, and I have confidence that it is 
                   appropriate and ethical. I am now in solo practice in the community and 
                   Lynne continues to do my billing. Fortunately, she bills for doctors in 
                   all practice environments and from so many different specialities that 
                   the transition was easy." 
               </p>
               <p><b>
               Dr. Kathleen Ferguson, Respirology and Sleep-Disordered Breathing
               </b> </p>
           </div>
           <div id= "two" className='testimonials'>
           <i class="fas fa-quote-left"></i>
               <p>
               "I have had the pleasure of working with Lynne Thibert for 9 years. 
               She has been consistently helpful, efficient and has given wise guidance 
               regarding billing and working in medical systems. She has maximized my earnings
                and minimized stressors in dealing with government, university and hospital 
                bureaucracies. Lynne is a pleasure to work with. ​I can enthusiastically recommend
                 Lynne and Dynamic Medical Services, without reservation."
               </p>
               <p><b>
               Dr. Ian W. K. Feltham, MD, CCFP, FCFP, FRCP
               </b>
               </p>
           </div>
           <div id= "three" className='testimonials'>
               <p>
               "I have used the services of Lynne Thibert of Dynamic Medical Services for
                the past 3 years, until my retirement. She has provided me with professional
                 service and good advice over the years."
               </p>
               <p><b>S.K. McMillan, Family Doctor Ancaster</b></p>
           </div>
           </div>
         
        </div>
    );
}

export default Testimonials;